*,
*::after,
*::before {
   -webkit-box-sizing: border-box;
   box-sizing: border-box;
   margin: 0;
   padding: 0;
   font-size: 100%;
   line-height: 1em;
   vertical-align: baseline;
}

::selection {
   background: #ccc;
   text-shadow: none;
}

blockquote,
q {
   quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
   content: '';
   content: none;
}

input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
   -webkit-appearance: none;
   -moz-appearance: none;
}

input[type=search] {
   -webkit-appearance: none;
   -moz-appearance: none;
   -webkit-box-sizing: content-box;
   box-sizing: content-box;
}

input[type="checkbox"],
input[type="radio"] {
   -webkit-box-sizing: border-box;
   box-sizing: border-box;
   padding: 0;
}

input[type="search"] {
   -webkit-appearance: textfield;
   -webkit-box-sizing: content-box;
   box-sizing: content-box;
}

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
   -webkit-appearance: none;
}

.chromeframe {
   margin: 0.2em 0;
   background: #ccc;
   color: #000;
   padding: 0.2em 0;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
   display: block;
}

ol,
ul,
li {
   display: block;
}

a {
   text-decoration: none;
   color: inherit;
}

audio,
canvas,
video {
   display: inline-block;
   max-width: 100%;
}

audio:not([controls]) {
   display: none;
   height: 0;
}

legend {
   border: 0;
   padding: 0;
   white-space: normal;
}

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
   -webkit-appearance: button;
   cursor: pointer;
}

button[disabled],
html input[disabled] {
   cursor: default;
}

table {
   border-collapse: collapse;
   border-spacing: 0;
}

img {
   display: block;
   vertical-align: middle;
   -o-object-fit: contain;
   object-fit: contain;
   width: 100%;
   height: 100%;
}

fieldset {
   border: 1px solid #ccc;
}

textarea {
   resize: vertical;
   overflow: auto;
   vertical-align: top;
}

button,
input,
textarea,
input,
select {
   display: block;
   border: none;
   background: none;
   font-family: inherit;
   color: inherit;
}

.hide {
   position: absolute;
   width: 1px;
   height: 1px;
   margin: -1px;
   border: 0;
   padding: 0;
   clip: rect(0 0 0 0);
   overflow: hidden;
}

html {
   font-size: 62.5%;
   scroll-behavior: smooth;
   height: 100%;
   /*  10px  = 1rem (1em) */
}

// *:where(:not(html, iframe, canvas, img, svg, video):not(svg *, symbol *)) {
//    all: unset;
